body {
  background-color: #f4dfc4;
  flex-direction: column;
  align-items: center;
  display: flex;
}

@font-face {
  font-family: OldLondon;
  src: url("OldLondon.ae71df83.ttf");
}

@font-face {
  font-family: Berkahi;
  src: url("Berkahi Blackletter.73012231.ttf");
}

@font-face {
  font-family: Typewriter;
  src: url("typewcond_regular.452d8221.otf");
}

@font-face {
  font-family: Typewriter_bold;
  src: url("typewcond_bold.bf826d36.otf");
}

main {
  border: 2px solid #483628;
  flex-direction: column;
  width: fit-content;
  display: flex;
}

header {
  background-color: #f4dfc488;
  border-top: 2px solid #372d3a;
  border-bottom: 2px solid #372d3a;
  width: 319px;
  position: fixed;
}

.title {
  border-top: 1px solid #473a3a;
  border-bottom: 1px solid #473a3a;
  justify-content: center;
  margin: 3px;
  display: flex;
}

nav {
  justify-content: space-around;
  width: auto;
  height: fit-content;
  display: flex;
}

a {
  color: #473a3a;
  margin: 2vb;
  font-family: Typewriter;
  font-size: 20px;
  text-decoration: none;
}

h1 {
  color: #483628;
  margin: 10px;
  font-family: Berkahi;
  font-size: 35px;
}

#try {
  flex-direction: column;
  display: flex;
}

.science_cont {
  padding-top: 122px;
}

.article1, .article2 {
  border-top: 2px solid #372d3a;
  border-bottom: 2px solid #372d3a;
  grid-template-columns: auto;
  place-items: center;
  margin: 1vb;
  display: grid;
}

.news_cont1, .news_cont2 {
  border-top: 1px solid #473a3a;
  border-bottom: 1px solid #473a3a;
  flex-direction: column;
  align-items: center;
  width: 95%;
  height: 97%;
  display: flex;
}

img {
  height: 170px;
  margin-top: 5px;
  margin-bottom: 5px;
}

h2 {
  color: #473a3a;
  align-self: center;
  margin: 2vb;
  font-family: Typewriter;
  font-size: 30px;
}

p {
  color: #473a3a;
  width: 230px;
  font-family: Typewriter;
  font-size: 12px;
}

h3 {
  color: #473a3a;
  width: 230px;
  font-family: Typewriter;
  font-size: 20px;
}

span {
  font-weight: bolder;
}

#logo {
  object-fit: contain;
  width: 200px;
}

footer {
  grid-template-columns: auto;
  place-items: center;
  width: 320px;
  height: 100px;
  display: grid;
}

#nyt_footer_lgo {
  height: fit-content;
  display: flex;
}

#logo {
  height: 10px;
}

.footer_text {
  text-align: center;
  width: 200px;
}

.news_cont1, .article1, h3, p {
  transition: color .7s, border-top .7s, border-bottom .7s, background-color .7s, box-shadow .7s;
}

.news_cont1:hover {
  border-top: 1px solid #f4dfc4;
  border-bottom: 1px solid #f4dfc4;
}

.article1:hover {
  background-color: #483628;
  box-shadow: 0 10px 10px #00000080;
}

.article1:hover h3, .article1:hover p {
  color: #f4dfc4;
}

.news_cont2, .article2, h3, p {
  transition: color .7s, border-top .7s, border-bottom .7s, background-color .7s, box-shadow .7s;
}

.news_cont2:hover {
  border-top: 1px solid #f4dfc4;
  border-bottom: 1px solid #f4dfc4;
}

.article2:hover {
  background-color: #483628;
  box-shadow: 0 10px 10px #00000080;
}

.article2:hover h3, .article2:hover p {
  color: #f4dfc4;
}

@media (width <= 320px) {
  .science_cont, .world_cont {
    flex-direction: column;
    margin: 1vb;
    display: flex;
  }
}

@media (width >= 1024px) {
  .science_cont, .world_cont {
    flex-direction: column;
    margin: 1vb;
    display: flex;
  }

  .science_cont {
    margin-top: 200px;
  }

  header {
    width: 1000px;
  }

  a {
    font-size: 2.5rem;
  }

  .title {
    margin: 1.2rem;
  }

  h1 {
    margin: 2rem;
    font-size: 5rem;
  }

  h2 {
    font-size: 3rem;
  }

  main {
    width: 1000px;
  }

  .article1, .article2 {
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
  }

  .news_cont1, .news_cont2 {
    grid-template-columns: auto auto;
    display: grid;
  }

  .right {
    order: 1;
  }

  p {
    width: 30rem;
    font-size: 1.2rem;
  }

  h3 {
    width: 30rem;
    font-size: 2rem;
  }

  .images {
    height: 15rem;
  }

  #logo {
    height: 40px;
  }
}
